.backdrop {
  opacity: 1;
  z-index: 99;
  background-color: #0003;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop__button {
  cursor: pointer;
  background-color: red;
  border: 1px solid #0000001a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
  position: fixed;
  top: 8px;
  right: 8px;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.9);
}

.backdrop__close {
  fill: #000;
}

.modal {
  background-color: #eee;
  max-width: 528px;
  min-height: 581px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

